body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header--gradient,
.landing-grid {
  text-align: center;

  background: #141e30; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#243b55),
    to(#141e30)
  );
  background: -webkit-linear-gradient(
    left,
    #243b55,
    #141e30
  );
  background: linear-gradient(
    to right,
    #243b55,
    #141e30
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid {
  /* //position: fixed; */
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
}

.avatar-img {
}

.avatar-img__container {
  display: flex;
  justify-content: center;
  height: 250px;
  width: auto;
  /* width: 240px; */
  margin-top: 4em;
  /* border-radius: 50%; */
  text-align: center;
}
.avatar-img__image {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
  border: 4px solid #182338bd;
  box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.1);
}
.banner-text {
  color: #fff;
  background-color: #152033;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}
.banner-text h1 {
  color: white;
  text-align: center;
  padding: 0.1em;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p {
  color: #fff;
  padding: 1em;
  font-size: 20px;
}
.social-link {
  display: flex;
  justify-content: space-around;
  align-content: center;
  width: 50%;
  margin: auto;
}
.social-link a {
  color: #fff;
  font-size: 3em;
  padding: 8px;
}

/* Contact page */
.contact {
  height: 100%;
  width: 100%;
}
.contact-grid {
  padding: 2em;
  height: 80%;
  /* // background-color: #fff; */
  text-align: center;
}
.contact-grid__img {
  height: 250px;
}
.contact-grid__img img {
  height: 100%;
  width: auto;
  border-radius: 10px 10px 0 0;
}
.contact-grid__info {
  font-family: "Roboto";
  font-size: 18px;
  margin: 2em 1em 0 1em;
  text-align: justify;
}

.contact-grid__links-header {
  font-family: "anton";
}
.contact-grid__links hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-grid__links-list {
  margin: auto;
}
.contact-grid__links-list span {
  font-size: 22px;
  font-family: "Anton";
}
.contact-grid__links-list i {
  font-size: 25px;

  margin-right: 1em;
}

.mdl-button--accent.mdl-button--accent.mdl-button--raised,
.mdl-button--accent.mdl-button--accent.mdl-button--fab {
  color: rgb(255, 255, 255);
  background-color: rgb(64, 130, 255);
  margin-top: 5em;
}
a {
  color: rgb(64, 130, 255);
}

/* Resume*/
.resume {
  height: 100%;
  min-width: 100%;
  /* //background-color: grey; */
}
.resume-left {
  height: 98%;
  /* margin: auto; */
  background: #fff;
  padding: 1em;
  border-radius: 10px;
  border-right: 10px dotted;
}
.resume-left__card {
  padding-top: 1.5em;
  margin-bottom: 0.5em;
}
.resume-left__card:hover {
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}
.resume-left__icon {
  font-family: "anton";
  font-size: 32px;
}
.resume-left__title {
  font-family: "anton";
  font-size: 22px;
}
.resume-left__subtitle {
  font-family: "Robto";
  font-size: 18px;
  padding-top: 1em;
}

.resume-right {
  min-height: 100vh;
  background: #27221f;
  color: #fff;
  padding-left: 2em;
}
.skill-title {
  font-family: "Roboto";
  padding-bottom: 5px;
  font-size: 16px;
  font-weight: 700;
  color: #27221e;
}
.skill-subtitle {
  font-size: 10px;
}
.skill {
  padding: 1em;
}
.skill:hover {
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}

